			this.messagelist[index].appreciate = !this.messagelist[index].appreciate
		},
		activityPage(id) {
			var page = getCurrentPages()
			page = page[page.length - 2]
			if (page.route == 'pages/activity/activity' && page.options.id == id) {
				uni.navigateBack()
			} else {
				uni.navigateTo({
					url: "../activity/activity?id=" + id
				})
			}
		},
